import * as dom from './wpstg-dom-utils';

class DarkMode {
  constructor(wpstgObject = wpstg) {
    this.wpstgObject = wpstgObject;
    document.addEventListener('DOMContentLoaded', async () => {
      this.body = document.querySelector('body');
      await this.init();
      this.observeDOMChanges();
    });
  }

  observeDOMChanges() {
    this.observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === 'childList' && mutation.addedNodes.length > 0) {
          this.triggerRenderEvent();
        }
      });
    });

    this.observerConfig = {
      childList: true,
      subtree: true,
    };

    this.observer.observe(document.documentElement, this.observerConfig);
  }

  triggerRenderEvent() {
    const isDarkMode = this.body.classList.contains('wpstg-admin-dark-mode');
    const dashicons = document.querySelectorAll('.wpstg--dashicons');
    dashicons.forEach((dashicon)=> {
      const wpstgIcon = dom.extractFileName(dashicon.src);
      if (wpstgIcon !== '' && !dashicon.src.includes('vendor/dashicons/dark/') && isDarkMode) {
        dashicon.src = this.wpstgObject.assetsUrl + 'svg/vendor/dashicons/dark/' + wpstgIcon;
      }

      if (wpstgIcon !== '' && dashicon.src.includes('vendor/dashicons/dark/') && !isDarkMode) {
        dashicon.src = this.wpstgObject.assetsUrl + 'svg/vendor/dashicons/' + wpstgIcon;
      }
    });
  }

  setButtonAsActive(mode) {
    const elements = ['dark', 'light', 'system'];
    elements.forEach((item) => {
      document.querySelector(`.wpstg-${item}-mode-container`).classList.remove('active');
    });

    document.querySelector(`.wpstg-${mode}-mode-container`).classList.add('active');

    const borderColor = (mode === 'dark' || (mode === 'system' && this.defaultOsColorMode === 'dark')) ? '#E3DCFF' : '#e9e9e9';

    document.querySelectorAll('.wpstg-color-mode-container > div').forEach((div) => {
      div.style.border = `1px solid ${borderColor}`;
    });
  }

  updateFooterLogo() {
    const logoUrl = this.body.classList.contains('wpstg-admin-dark-mode') ? 'img/dark-logo.svg' : 'img/logo.svg';
    const footerLogoAnchor = document.querySelector('.wpstg-footer-logo a');
    if (footerLogoAnchor) {
      const footerLogoImg = footerLogoAnchor.querySelector('img');
      if (footerLogoImg) {
        footerLogoImg.src = this.wpstgObject.assetsUrl + logoUrl;
      }
    }

    this.triggerRenderEvent();
  }

  updateBodyClass(mode) {
    if (mode === 'dark' || (mode === 'system' && this.defaultOsColorMode === 'dark')) {
      this.body.classList.add('wpstg-admin-dark-mode');
    } else {
      this.body.classList.remove('wpstg-admin-dark-mode');
    }

    this.updateFooterLogo();
  }

  setButtonsEventListeners() {
    this.systemModeButton = document.querySelector('#wpstg-system-mode-button');
    this.lightModeButton = document.querySelector('#wpstg-light-mode-button');
    this.darkModeButton = document.querySelector('#wpstg-dark-mode-button');

    this.systemModeButton.addEventListener('click', async () => {
      await this.setMode('system');
      this.setButtonAsActive('system');
    });

    this.lightModeButton.addEventListener('click', async () => {
      await this.setMode('light');
      this.setButtonAsActive('light');
    });

    this.darkModeButton.addEventListener('click', async () => {
      await this.setMode('dark');
      this.setButtonAsActive('dark');
    });
  }

  async init() {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    this.defaultOsColorMode = mediaQuery.matches ? 'dark' : 'light';

    this.setButtonsEventListeners();
    await this.setDefaultOsColorMode();

    mediaQuery.addEventListener('change', async (event) => {
      this.defaultOsColorMode = mediaQuery.matches ? 'dark' : 'light';
      await this.setDefaultOsColorMode();
    });
  }

  async setMode(mode) {
    try {
      const response = await fetch(this.wpstgObject.ajaxUrl, {
        method: 'POST',
        credentials: 'same-origin',
        body: new URLSearchParams({
          action: 'wpstg_set_dark_mode',
          accessToken: this.wpstgObject.accessToken,
          nonce: this.wpstgObject.nonce,
          mode: mode,
        }),
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      });

      if (!response.ok) {
        dom.notify('error', this.wpstgObject.i18n['somethingWentWrong']);
        return;
      }

      const responseData = await response.json();
      if (responseData.success === false && responseData.data) {
        dom.notify('error', responseData.data);
        return;
      }

      this.updateBodyClass(mode);
    } catch (error) {
      console.warn(this.wpstgObject.i18n['somethingWentWrong'], error);
    }
  }


  async setDefaultOsColorMode() {
    try {
      const response = await fetch(this.wpstgObject.ajaxUrl, {
        method: 'POST',
        credentials: 'same-origin',
        body: new URLSearchParams({
          action: 'wpstg_set_default_os_color_mode',
          accessToken: this.wpstgObject.accessToken,
          nonce: this.wpstgObject.nonce,
          defaultOsColorMode: this.defaultOsColorMode,
        }),
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      });

      if (!response.ok) {
        dom.notify('error', this.wpstgObject.i18n['somethingWentWrong']);
        return;
      }

      const responseData = await response.json();

      if (responseData.data && responseData.data.defaultColorMode) {
        this.setButtonAsActive(responseData.data.defaultColorMode);
      }

      if (responseData.success && responseData.data.defaultColorMode === 'dark') {
        this.body.classList.add('wpstg-admin-dark-mode');
        this.updateFooterLogo();
        return;
      }

      if (responseData.success && this.defaultOsColorMode === 'dark' && responseData.data.defaultColorMode === 'system') {
        this.body.classList.add('wpstg-admin-dark-mode');
        this.updateFooterLogo();
        return;
      }

      if (responseData.success && this.body.classList.contains('wpstg-admin-dark-mode')) {
        this.body.classList.remove('wpstg-admin-dark-mode');
        this.updateFooterLogo();
      }
    } catch (error) {
      console.warn(this.wpstgObject.i18n['somethingWentWrong'], error);
    }
  }
}

new DarkMode();
