import * as dom from './wpstg-dom-utils.js';
import WPStagingCommon from '../wpstg';

export default class WpstgSystemInfo {
  constructor(wpstgObject = wpstg) {
    this.wpstgObject = wpstgObject;
    this.wpstgPurgeBackupQueueButton = dom.qs('#wpstg-purge-backup-queue-btn');
    this.init();
  }

  /**
   * @return {void}
   */
  init() {
    if (this.wpstgPurgeBackupQueueButton !== null) {
      this.wpstgPurgeBackupQueueButton.addEventListener('click', () => {
        this.confirmAndProceedPurge();
      });
    }
  }

  /**
   * @return {void}
   */
  confirmAndProceedPurge() {
    WPStagingCommon.getSwalModal(true, {
      container: 'wpstg-swal-push-container',
      confirmButton: 'wpstg--btn--confirm wpstg-blue-primary wpstg-button wpstg-link-btn',
    }).fire({
      title: 'Are You Sure?',
      icon: 'warning',
      html: '<p>This cleans up the database table <code>' + this.wpstgObject.tblprefix+ 'wpstg_queue</code>. Only use this function for debugging purposes, or if the scheduled backups do not work as expected.</p>',
      width: '550px',
      focusConfirm: false,
      confirmButtonText: 'Clean Backup Queue',
      showCancelButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.ajaxPurgeQueueTable();
      }
    });
  }

  /**
   * @return {void}
   */
  ajaxPurgeQueueTable() {
    fetch(this.wpstgObject.ajaxUrl, {
      method: 'POST',
      credentials: 'same-origin',
      body: new URLSearchParams({
        action: 'wpstg_purge_queue_table',
        accessToken: this.wpstgObject.accessToken,
        nonce: this.wpstgObject.nonce,
      }),
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    }).then((response) => {
      if (response.ok) {
        return response.json();
      }

      if (response.status === 404) {
        dom.notify('error', this.wpstgObject.i18n['somethingWentWrong'] + ' can\'t find ajax request URL! Please contact us at support@wp-staging.com');
      }

      if (response.status === 500) {
        dom.notify('error', this.wpstgObject.i18n['somethingWentWrong'] + ' internal server error while processing the request! Please contact us at support@wp-staging.com');
      }
    }).then((response) => {
      if (response.success && response.success === true) {
        dom.notify('success', response.message);
      }

      if (response.success && response.success === false) {
        dom.notify('error', response.message);
      }
    }).catch((error) => {
      dom.notify('error', this.wpstgObject.i18n['somethingWentWrong'] + ' ' + error);
      console.warn(this.wpstgObject.i18n['somethingWentWrong'], error);
    });
  }
}
